import { Layout, Menu, Dropdown, Button } from "antd";
import { UserOutlined } from "@ant-design/icons";
import LoginForm from "./components/LoginForm";
import SignupForm from "./components/SignupForm";
import frontpage from "./images/frontpage.jpeg";
import React, { useState } from "react";
import SuppliesList from "./components/SuppliesList";
import MyCart from "./components/MyCart";

const { Header, Content } = Layout;

function App() {
  const [authed, setAuthed] = useState(false);

  const handleLogOut = () => {
    setAuthed(false);
  };

  const userMenu = (
    <Menu>
      <Menu.Item key="logout" onClick={handleLogOut}>
        Log Out
      </Menu.Item>
    </Menu>
  );

  return (
    <Layout style={{ height: "100vh" }}>
      <Header
        style={{
          position: "fixed",
          width: "100%",
          background: "#8FBC8F",
          opacity: "90%",
          display: "flex",
          justifyContent: "space-between",
          gap: "20px",
          height: 55,

          zIndex: 2,
        }}
      >
        <div
          style={{
            fontSize: 25,
            flexGrow: "1",
            // fontStyle: "italic",
            fontWeight: "bold",
            color: "white",

            fontFamily: "helvetica",
          }}
        >
          Blick+ Art Supplies
        </div>
        <div>{authed ? <MyCart /> : <SignupForm />}</div>
        {authed && (
          <div>
            <Dropdown trigger="click" overlay={userMenu}>
              <Button
                style={{
                  top: "-5px",
                  flexGrow: "3",
                }}
                icon={<UserOutlined />}
                shape="circle"
              />
            </Dropdown>
          </div>
        )}
      </Header>

      <img
        src={frontpage}
        alt="ArtSupplies"
        style={{
          position: "relative",
          // height: "100%",
          flexShrink: "0",
          width: "100%",
          // height: "100%",
          // opacity: "80%",
          zIndex: 0,
        }}
      />

      <Content
        style={{
          top: "100px",
          left: "80px",
          position: "fixed",
          height: "calc(100% - 64px)",
          // fontcolor: "white",
          margin: 20,
          overflow: "auto",
          zIndex: 2,
        }}
      >
        {authed ? (
          <SuppliesList />
        ) : (
          <LoginForm onSuccess={() => setAuthed(true)} />
        )}
      </Content>
      <div
        style={{
          color: "white",
          bottom: "50px",
          textAlign: "center",
          zIndex: 1,
          position: "relative",
        }}
      >
        ©2022 Blick+ Art Supplies. All Rights Reserved. Website Made by XD.
      </div>
      {/* <Footer style={{ textAlign: "center" }}>
        ©2022 YummyFood Ordering System. All Rights Reserved. Website Made by XD
      </Footer> */}
    </Layout>
  );
}

export default App;
