import { Button, Card, List, message, Select, Tooltip } from "antd";
import { useEffect, useState } from "react";
import { addItemToCart, getMenus, getSupplies } from "../utils";
import { PlusOutlined } from "@ant-design/icons";

const { Option } = Select;

const AddToCartButton = ({ itemId }) => {
  const [loading, setLoading] = useState(false);

  //call API
  const AddToCart = () => {
    setLoading(true);
    addItemToCart(itemId)
      .then(() => message.success(`Successfully add item`))
      .catch((err) => message.error(err.message))
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <Tooltip title="Add to shopping cart">
      <Button
        style={{
          border: "2px solid #8FBC8F",
          backgroundColor: "#8FBC8F",
          borderRadius: "10px",
        }}
        loading={loading}
        type="primary"
        icon={
          <PlusOutlined
            style={{
              type: "setting",
              color: "white",
            }}
          />
        }
        onClick={AddToCart}
      />
    </Tooltip>
  );
};

const SuppliesList = () => {
  const [suppliesData, setSuppliesData] = useState([]);
  const [curRest, setCurRest] = useState(); //the curRest the customer selects
  const [supplies, setSupplies] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingRest, setLoadingRest] = useState(false);
  const { Meta } = Card;

  //run on the first rendering
  useEffect(() => {
    setLoadingRest(true);
    getSupplies()
      .then((data) => {
        setSupplies(data);
      })
      .catch((err) => {
        message.error(err.message);
      })
      .finally(() => {
        setLoadingRest(false);
      });
  }, []);

  useEffect(() => {
    if (curRest) {
      setLoading(true);
      getMenus(curRest)
        .then((data) => {
          setSuppliesData(data);
        })
        .catch((err) => {
          message.error(err.message);
        })
        .finally(() => {
          setLoading(false);
        });
    }
  }, [curRest]);

  return (
    <>
      <Select
        title="Supplies"
        value={curRest}
        onSelect={(value) => setCurRest(value)}
        placeholder="Select your art supplies"
        loading={loadingRest} //getSupplies
        style={{ width: 300 }}
      >
        {supplies.map((item) => {
          return <Option value={item.id}>{item.name}</Option>;
        })}
      </Select>

      {/* if curRest is true */}
      {curRest && (
        <List
          style={{ marginTop: 20 }}
          loading={loading}
          grid={{
            gutter: 16,
            xs: 1,
            sm: 2,
            md: 4,
            lg: 4,
            xl: 4,
            xxl: 4,
          }}
          dataSource={suppliesData}
          renderItem={(item) => (
            <List.Item>
              <Card
                hoverable
                style={{
                  //
                  // display: "flex",
                  // height: "100%",
                  // flexDirection: "column",
                  // flexGrow: "1",
                  // height: "100%",
                  fontWeight: "bold",
                  border: "15px solid white",
                  borderRadius: "15px",
                }}
                extra={<AddToCartButton itemId={item.id} />}
                cover={
                  <img
                    style={{
                      padding: "50px",
                      height: "300px",
                      width: "100%",
                      // maxWidth: "450px",
                      // scale: "initial",
                      // alignItems: "center",
                      // alignContent: "initial",
                      // maxWidth: "360px",

                      display: "block",
                      zIndex: 3,
                    }}
                    alt={item.name}
                    src={item.imageUrl}
                  />
                }
              >
                <Meta
                  style={{
                    fontFamily: "helvetica",
                    fontStyle: "italic",
                  }}
                  title={item.name}
                />
                {`$${item.price}`}
              </Card>
              {/* <Card
                style={{ border: "15px solid white", borderRadius: "5px" }}
                title={item.name}
                extra={<AddToCartButton itemId={item.id} />}
              >
                <img
                  src={item.imageUrl}
                  alt={item.name}
                  style={{
                    height: 340,
                    // width: "100%",
                    display: "block",
                    zIndex: 3,
                  }}
                />
                {`$${item.price}`}
              </Card> */}
            </List.Item>
          )}
        />
      )}
    </>
  );
};

export default SuppliesList;
