//1. API call

export const login = (credential) => {
  const loginUrl = `login?username=${credential.username}&password=${credential.password}`;

  //window provides the fetch function
  return fetch(loginUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    credentials: "include", //saved in the browser cookie
  }).then((response) => {
    if (response.status < 200 || response.status >= 300) {
      throw Error("Fail to log in");
    }
  });
};
//send request -> receive response -> .then

export const signup = (data) => {
  const signupUrl = "/signup";
  return fetch(signupUrl, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(data),
  }).then((response) => {
    if (response.status < 200 || response.status >= 300) {
      throw Error("Fail to sign up");
    }
  });
};

export const getMenus = (suppliesId) => {
  return fetch(`/supplies/${suppliesId}/menu`).then((response) => {
    if (response.status < 200 || response.status >= 300) {
      throw Error("Fail to get menus");
    }

    return response.json();
  });
};

